import { ethers } from "ethers";
import React from "react";

export type Dispatch = React.Dispatch<IAction>;

export interface IState {
  refreshing: IRefreshing;
  wallet: IWallet;
  walletWeb3Modal: IWalletWeb3Modal;
  queryResults: IQueryResults;
}

export interface IAction {
  type: string;
  payload: any;
}

export interface IRefreshing {
  status: boolean;
  message: string;
}

export const defaultRefreshing = {
  status: false,
  message: "Please wait a few seconds...",
};

// Interfaces.tsx
export interface IWallet {
  walletProviderName: string; // for example, "metamask" or "defiwallet"
  address: string; // 0x address of the user
  browserWeb3Provider: any; // Web3 provider connected to the wallet's browser extension
  serverWeb3Provider: ethers.providers.JsonRpcProvider | null; // cloud based Web3 provider for read-only
  wcConnector: any; // connector object provided by some wallet connection methods, stored if relevant
  wcProvider: any; // provider object provided by some wallet connection methods, stored if relevant
  connected: boolean; // is the wallet connected to the Dapp, or not?
  chainId: number; // for example, 25 for Cronos mainnet, and 338 for Cronos testnet
}

export const defaultWallet: IWallet = {
  walletProviderName: "",
  address: "",
  browserWeb3Provider: null,
  serverWeb3Provider: null,
  wcConnector: null,
  wcProvider: null,
  connected: false,
  chainId: 0,
};

export interface IWalletWeb3Modal {
  fetching: boolean;
  address: string;
  web3: any;
  provider: any;
  connected: boolean;
  chainId: number;
  networkId: number;
  assets: IAssetData[];
  showModal: boolean;
  pendingRequest: boolean;
  result: any | null;
}

export const defaultWalletWeb3Modal: IWalletWeb3Modal = {
  fetching: false,
  address: "",
  web3: null,
  provider: null,
  connected: false,
  chainId: 1,
  networkId: 1,
  assets: [],
  showModal: false,
  pendingRequest: false,
  result: null,
};

export interface IAssetData {
  symbol: string;
  name: string;
  decimals: string;
  contractAddress: string;
  balance?: string;
}

export interface IQueryResults {
  lastBlockNumber: number | undefined;
  croBalance: number| undefined;
  crooksBalance: number| undefined;
  croRewards: number| undefined;
  rewardThreshold: number| undefined;
  lastTxHash: string;
}

export const defaultQueryResults: IQueryResults = {
  lastBlockNumber: undefined,
  croBalance: undefined,
  crooksBalance: undefined,
  croRewards: undefined,
  rewardThreshold: 5000,
  lastTxHash: "",
};

export interface Pair {
  chainId: string;
  dexId: string;
  url: string;
  pairAddress: string;
  baseToken: {
    address: string;
    name: string;
    symbol: string;
  };
  quoteToken: {
    symbol: string;
  };
  priceNative: string;
  priceUsd?: string;
  txns: {
    m5: {
      buys: number;
      sells: number;
    };
    h1: {
      buys: number;
      sells: number;
    };
    h6: {
      buys: number;
      sells: number;
    };
    h24: {
      buys: number;
      sells: number;
    };
  };
  volume: {
    m5: number;
    h1: number;
    h6: number;
    h24: number;
  };
  priceChange: {
    m5: number;
    h1: number;
    h6: number;
    h24: number;
  };
  liquidity?: {
    usd?: number;
    base: number;
    quote: number;
  };
  fdv?: number;
  pairCreatedAt?: number;
}
