import React from "react";
import ReactDOM from "react-dom";
import { HashRouter /*BrowserRouter*/ } from "react-router-dom";
import "./index.css";
import App from "./App";
import { StoreProvider } from "./store/store-reducer";

ReactDOM.render(
  <React.StrictMode>
    <StoreProvider>
      <HashRouter>
        <App />
      </HashRouter>
    </StoreProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
