import React from "react";
import { Store } from "../store/store-reducer";

import { styled } from "@mui/material/styles";
import { Box, Button, Typography } from "@mui/material";
import Paper from "@mui/material/Paper";

import * as utils from "../helpers/utils";
import { Pair } from "../store/interfaces";
import {
  updateQueryResultsAction,
  updateRefreshingAction,
} from "../store/actions";

const ActionButton = styled(Button)({
  marginTop: "20px",
  marginLeft: "20px",
  padding: "6px 12px",
});

interface IProps {}

const Welcome: React.FC<IProps> = () => {
  const { state, dispatch } = React.useContext(Store);

  const [stats, setStats] = React.useState<Pair>();

  React.useEffect(()=> {
    utils.getCrooksStats().then((p) => {console.log(p); setStats(p);} );
  }, []);

  const claimRewards = async () => {
    updateRefreshingAction(dispatch, {
      status: true,
      message: "Claiming rewards...",
    });
    const rewardsInstance = await utils.getWriteContractInstance(
      state.wallet.browserWeb3Provider
    );
    const tx = await rewardsInstance["claim"]();
    console.log("CLAIM TX", tx.hash);
    updateRefreshingAction(dispatch, {
      status: false,
      message: "Complete",
    });
    updateQueryResultsAction(dispatch, {
      ...state.queryResults,
      croRewards: 0,
      lastTxHash: tx.hash,
    });
  };

  return (
    <>
      <Box
        sx={{
          flexGrow: 1,
          m: 5,
          minHeight: "500px",
        }}
      >
        <Paper elevation={0}>
          <Typography variant="h5" component="div" gutterBottom>
            Welcome to CROOKS FINANCE
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            $CRO balance:{" "}
            {state.queryResults.croBalance
              ? state.queryResults.croBalance
              : "Not connected"}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            $CROOKS balance:{" "}
            {state.queryResults.crooksBalance !== undefined
              ? state.queryResults.crooksBalance.toLocaleString()
              : "Not connected"}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Rewards percentage: {state.queryResults.crooksBalance  !== undefined
              ? state.queryResults.crooksBalance < state.queryResults.rewardThreshold 
                ? `Need to hodl ${state.queryResults.rewardThreshold.toLocaleString()} $CRKS to be eligiable for rewards` 
                : `${(parseFloat(state.queryResults.crooksBalance) / 1000000).toFixed(2)} %` 
              : 'Not connected'}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Pending rewards:{" "}
            {state.queryResults.croRewards  !== undefined
              ? `${state.queryResults.croRewards} $CRO`
              : "Not connected"}
          </Typography>
          {state.queryResults.croRewards > 0
            ? <ActionButton variant="contained" onClick={claimRewards}>Claim</ActionButton>
            : <></>
          }
        </Paper>

        <Paper elevation={0}>
          <Typography variant="h5" component="div" gutterBottom>
            Crooks stats
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Price Change: {stats?.priceChange.h24 ? `${stats?.priceChange.h24} % (24H)` : ''}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Price: {stats?.priceNative ? `${ (1 / parseFloat(stats.priceNative)).toFixed(2)} $CRKS per $CRO` : ''}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Price: {stats?.priceUsd ? `${ (1 / parseFloat(stats.priceUsd)).toFixed(2)} $CRKS per $USD` : ''}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Liquidity: {stats?.liquidity?.usd ? `${stats?.liquidity.usd.toFixed(2)} $USD (${stats?.liquidity?.quote.toLocaleString()} WCRO)` : ''}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Volume: {stats?.volume.h24 ? `${stats?.volume.h24} $USD (24H)` : ''}
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            Buys / Sells: {stats?.txns?.h24?.buys ? `${stats?.txns.h24.buys}` : ''} / {stats?.txns?.h24?.sells ? `${stats?.txns.h24.sells}` : ''} (24H)
          </Typography>
        </Paper>
        <Paper elevation={0}>
          <Typography variant="h5" component="div" gutterBottom>
            Farms 
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
              
          </Typography>
          
        </Paper>
        <Paper elevation={0}>
          <Typography variant="h5" component="div" gutterBottom>
            Wallets 
          </Typography>
          <Typography variant="body1" component="div" gutterBottom>
            The wallets listed below are team / community wallets and not able to sell
          </Typography>

        </Paper>
      </Box>
    </>
  );
};

export default Welcome;
